.heading {
  font-size: 50px;
  font-family: serif;
  font-weight: normal;
  text-align: center;
}

.browseCats {
  display: grid;
  grid-gap: 10px;
  width: 80%;
  margin: 0 auto;
  padding: 10px;
  align-items: center;
}

.browseCats .one,
.browseCats .two,
.browseCats .three,
.browseCats .four,
.browseCats .five,
.browseCats .six {
  background-color: #fafafa;
  display: grid;
  grid-template-columns: 30% auto;
  box-shadow: 3px 3px #ebe8e1;
  cursor: pointer;
}

.browseCats .one img,
.browseCats .two img,
.browseCats .three img,
.browseCats .four img,
.browseCats .five img,
.browseCats .six img {
  height: 120px;
  width: auto;
}

.browseCats .one .tag,
.browseCats .two .tag,
.browseCats .three .tag,
.browseCats .four .tag,
.browseCats .five .tag,
.browseCats .six .tag {
  text-align: center;
  margin: auto auto;
  font-family: serif;
  font-size: 30px;
}

@media only screen and (max-width: 629px) {
  .browseCats .one,
  .browseCats .two,
  .browseCats .three,
  .browseCats .four,
  .browseCats .five,
  .browseCats .six {
    display: block;
  }

  .browseCats .one img,
  .browseCats .two img,
  .browseCats .three img,
  .browseCats .four img,
  .browseCats .five img,
  .browseCats .six img {
    height: 125px;
    width: auto;
    margin-left: 30%;
  }
}

